// utils
import moment from 'moment'
import {
  each,
  isArray,
  isObject,
  merge
} from 'lodash'

// 把一个object数据清空
export const empty = obj => {
  let o = {}
  each(obj, (r, k) => {
    if (isObject(r)) {
      o[k] = isArray(r) ? [] : {}
    } else {
      if (r !== '') {
        o[k] = r
      }
    }
  })
  return o
}
// 把一个object本身数据清空 可传指定keys 数组
export const emptySelf = (obj, keys) => {
  if (keys && keys.length) {
    keys.map(item => {
      if (obj[item]) {
        if (isArray(obj[item])) {
          obj[item] = []
        }
        if (isObject(obj[item])) {
          obj[item] = emptySelf(item)
        } else {
          obj[item] = ''
        }
      }
    })
    return obj
  }
  each(obj, (item, key) => {
    if (item !== null) {
      if (isArray(item)) {
        obj[key] = []
      } else {
        if (isObject(item)) {
          obj[key] = emptySelf(item)
        } else {
          obj[key] = ''
        }
      }
    }
  })
  return obj
}

// 把一个object数据结构深度清空
export const emptyDeep = obj => {
  let o = {}
  each(obj, (item, key) => {
    if (item !== null) {
      if (isArray(item)) {
        o[key] = []
      } else {
        if (isObject(item)) {
          o[key] = emptyDeep(item)
        } else {
          o[key] = ''
        }
      }
    }
  })
  return o
}

// 手机打码
export const maskmobile = mobile => {
  if (!mobile) return
  return mobile.replace(/(\d{3})\d{4}(\d{4})/, '$1****$2')
}

// 身份证打码
export const maskIdNumber = id => {
  return id.replace(/^(.{3})(.*)(.{4})$/, '$1********$3')
}

// 姓名打码
export const maskName = name => {
  if (name.length < 2) {
    return
  }
  let arr = name.split('')
  if (arr.length === 2) {
    return arr[0] + '*'
  } else {
    return arr[0] + '*' + (arr[arr.length - 1])
  }
}

/**
 * 校验手机号
 */
export const isPhone = phone => {
  return /^1[0-9]{10}$/.test(phone)
}
/**
 * 只能输入0 或非零开头的数字
 */
export const isInteger = value => {
  return /^(0|[1-9][0-9]*)$/.test(value)
}
/**
 * 校验金额
 */
export const isMoney = value => {
  return /(^[1-9]([0-9]+)?(\.[0-9]{1,4})?$)|(^(0){1}$)|(^[0-9]\.[0-9]([0-9])?$)/.test(value)
}
/**
 * 校验密码
 */
export const isPassword = value => {
  return /^(?=.*[A-Za-z])(?=.*\d)[A-Za-z\d]{6,18}$/.test(value)
}
/**
 * 验证小数2位或4位
 */
export const isDecimal = value => {
  return /^\d{0,8}\.{0,1}((\d{2})|(\d{4}))?$/.test(value)
}
/**
 * 验证小数4位
 */
export const isDecimalFour = value => {
  return /^\d+(\.\d{1,4})?$/.test(value)
}
/**
 * 验证小数2位
 */
export const isDouDecimal = value => {
  return /^\d+(\.\d{1,2})?$/.test(value)
}
/**
 * 格式化时间
 */
export const formatDate = (value, pattern) => {
  pattern = pattern || 'YYYY-MM-DD HH:mm'
  if (!value || value === '') return ''
  return moment(value).format(pattern)
}
/**
 * 格式化范围
 */
export const formatScope = value => {
  if (!value.length) return ''
  return value
}

/**
 * 获取url数据
 */
export const getUrlParam = name => {
  let reg = new RegExp('(^|&)' + name + '=([^&]*)(&|$)') // 构造一个含有目标参数的正则表达式对象
  let r = window.location.search.substr(1).match(reg) // 匹配目标参数
  if (r !== null) return unescape(r[2])
  return null // 返回参数值
}
/**
 * 合并表头
 */
export const mergeHeader = (header, data) => {
  header.map(item => {
    data.map(items => {
      if (item.headerKey === items.headerKey) {
        merge(item, items)
      }
    })
  })
}

/**
 * 证件号 只能输入数字或字母
 */
export const checkCard = value => {
  return /^[a-zA-Z\d]+$/.test(value)
}
/**
 * 证件号 只能输入数字或字母或括号 香港身份证校验
 */
export const checkCard3 = value => {
  return /[A-Z][0-9]{6}\([0-9A]\)/.test(value)
}
/**
 * 证件号 只能输入数字或字母或括号 台湾身份证校验
 */
export const checkCardTW = value => {
  return /[A-Z][0-9]{9}/.test(value)
}
/**
 * 证件号 只能输入数字或字母或汉字
 */
export const checkCard2 = value => {
  return /^[a-zA-Z\d\u4e00-\u9fa5]+$/.test(value)
}
/**
 * 获取元素距离顶部左部边框的距离
 */
export const getAbsPoint = (e) => {
  let x = 0
  let y = 0
  add(e)
  function add (e) {
    x += e.offsetLeft
    y += e.offsetTop
    if (e.offsetParent) {
      e = e.offsetParent
      add(e)
    }
  }
  return {'x': x, 'y': y}
}
/**
*验证 0-99 之间的整数
*/
export const isNine = value => {
  return /^([1-9]\d|\d)$/.test(value)
}
/**
*验证 1-99 之间的整数
*/
export const isFtoN = value => {
  return /^([1-9]\d?|99)$/.test(value)
}
/**
*验证 1-999之间的整数
*/
export const isFtoHn = value => {
  return /^[1-9][0-9]{0,2}$/.test(value)
}
/**
*验证 https://或者https://开头
*/
export const urlHeader = value => {
  return /^[1-9][0-9]{0,2}$/.test(value)
}

/**
 * 正整数
 */
export const isPositiveInteger = value => {
  return /^(0|[1-9][0-9]*)$/.test(value)
}
/**
 * 非0正整数
 */
export const isEcpInteger = value => {
  return /^\+?[1-9][0-9]*$/.test(value)
}
/**
* 最多四位小数
*/
export const isFourDecimal = value => {
  return /^([1-9]\d{0,200}|0)(\.\d{1,4})?$/.test(value)
}
/**
* 1-9999
*/
export const isToFn = value => {
  return /^[1-9][0-9]{0,3}$/.test(value)
}
/**
* 1-30正整数
*/
export const isToTh = value => {
  return /^([12][0-9]|30|[1-9])$/.test(value)
}
/**
* 正整数(含0 最多两位小数)
*/
export const interTwo = value => {
  return /^(([0-9][0-9]*)|(([0]\.\d{1,2}|[1-9][0-9]*\.\d{1,2})))$/.test(value)
}
/**
 * 邮箱
 */
export const email = value => {
  return /[\w!#$%&'*+/=?^_`{|}~-]+(?:\.[\w!#$%&'*+/=?^_`{|}~-]+)*@(?:[\w](?:[\w-]*[\w])?\.)+[\w](?:[\w-]*[\w])?/.test(value)
}
/**
 *  正数，且最多保留两位小数
*/
export const isTwDec = value => {
  return /^(?!0+(?:\.0+)?$)(?:[1-9]\d*|0)(?:\.\d{1,2})?$/.test(value)
}
/**
 *  最多保留两位小数
*/
export const isTwMoney = value => {
  return /^[+-]?\d*\.?\d{1,2}$/.test(value)
}
/**
 *  最多保留四位小数
*/
export const isFourMoney = value => {
  return /^[+-]?\d*\.?\d{1,4}$/.test(value)
}
/**
* 0-100(不包含0)，且最多保留两位小数
*/
export const isTwHun = value => {
  return /^\d\.([1-9]{1,2}|[0-9][1-9])$|^[1-9]\d{0,1}(\.\d{1,2}){0,1}$|^100(\.0{1,2}){0,1}$/.test(value)
}

/**
 * 获取url参数
 */
export const getQueryParams = (name) => {
  let reg = new RegExp('(^|&)' + name + '=([^&]*)(&|$)', 'i')
  let r = window.location.search.substr(1).match(reg)
  if (r !== null) {
    return unescape(r[2])
  }
  return null
}
/**
 * 接口数据编码
 */
export const Encode = value => {
  if (Object.prototype.toString.call(value) === '[object String]') {
    value = encodeURIComponent(value)
  } else if (Object.prototype.toString.call(value) === '[object Object]') {
    for (let i in value) {
      if (Object.prototype.toString.call(value[i]) === '[object String]') {
        value[i] = encodeURIComponent(value[i])
      } else if (Object.prototype.toString.call(value[i]) === '[object Object]') {
        Encode(value[i])
      } else if (Object.prototype.toString.call(value[i]) === '[object Array]') {
        for (let o = 0; o < value[i].length; o++) {
          value[i][o] = Encode(value[i][o])
        }
      }
    }
  } else if (Object.prototype.toString.call(value) === '[object Array]') {
    value.map(item => {
      Encode(item)
    })
  }
  return value
}
