// localStorage plugin
import localStorage from '@/common/localstorage'

function plugin (Vue) {
  if (plugin.installed) {
    return
  }
  Vue.localStorage = localStorage

  Object.defineProperties(Vue.prototype, {
    $localStorage: {
      get () {
        return localStorage
      }
    }
  })
}

if (typeof window !== 'undefined' && window.Vue) {
  window.Vue.use(plugin)
}

export default plugin
