import Axios from 'axios'
// import {merge, isString, cloneDeep} from 'lodash'
import {
  merge
} from 'lodash'
import Store from '@/store'
import {
  SignData
} from '@/common/sign'
import {
  Loading,
  Message
} from 'nb-vue-element'
import {
  Encode
} from '@/common/utils'
import moment from 'moment'

function plugin (Vue) {
  if (plugin.installed) {
    return
  }
  let loading = null

  // const append = (obj, key, val) => {
  //   let isStr = isString(obj)
  //   let o = isStr ? JSON.parse(obj) : cloneDeep(obj)
  //   o[key] = val
  //   obj = isStr ? JSON.stringify(o) : o
  //   return obj
  // }

  // 请求拦截
  Axios.interceptors.request.use(config => {
    let token = Vue.localStorage.get(Vue.const.COOKIE.KEY_TOKEN)
    if (Vue.localStorage.get(Vue.const.COOKIE.KEY_USER) && config.url.match(/(old|bbd|http)/)) {
      config.headers.entId = Vue.localStorage.get(Vue.const.COOKIE.KEY_USER).entId
    }
    if (config.headers['jsonType'] === 'stringify') {
      let _data = {}
      if (token) {
        _data.token = token || ''
      }
      _data = merge(_data, config.data)
      _data.sign = SignData.signFun(_data.param)
      _data = `data=${JSON.stringify(_data)}`
      config.data = _data
    } else if (config.headers['api_new'] === 'true') {
      config.headers.token = Vue.localStorage.get(Vue.const.COOKIE.KEY_TOKEN)
      let _data = {}
      _data.timestamp = moment(new Date()).format('YYYY-MM-DD HH:mm:ss')
      _data.app_key = 'test'
      _data.format = 'json'
      _data.version = ''
      if (token) {
        _data.access_token = token || ''
      }
      _data = merge(_data, config.data)
      _data.data = encodeURIComponent(JSON.stringify(_data.data))
      _data.sign = SignData.signNew(_data)
      config.data = _data
    } else if (config.market) {
      let _data = config.data
      _data.data = encodeURIComponent(JSON.stringify(_data.data))
      _data.access_token = token || ''
      _data.version = ''
      config.data = _data
    } else if (config.headers['Content-Type'] === 'application/x-www-form-urlencoded' && config.headers['isNewApi'] !== true) {
      let _data = {}
      if (token) {
        _data.token = token || ''
      }
      _data = merge(_data, config.data)
      _data.sign = SignData.signFun(_data.param)
      _data.param = Encode(_data.param)
      _data = `data=${JSON.stringify(_data)}`
      config.data = _data
    } else if (config.headers['Content-Type'] === 'application/x-www-form-urlencoded' && config.headers['isNewApi'] === true) {
    } else if (config.headers['ex'] === true) {
      config.headers.token = Vue.localStorage.get(Vue.const.COOKIE.KEY_TOKEN)
      config.headers['serviceChannel'] = 'WBS'
    } else if (config.headers['fof'] === true) {
      config.headers.Authorization = `Bearer ${token}`
      config.headers['Wbs-Host'] = window.location.host
    } else {
      if (config.method === 'post' && !config.headers['body_data']) {
        config.headers.token = Vue.localStorage.get(Vue.const.COOKIE.KEY_TOKEN)
        config.data.append('token', token)
      }
      if (config.headers['body_data']) {
        config.headers.token = Vue.localStorage.get(Vue.const.COOKIE.KEY_TOKEN)
      }
    }
    if (config.loading !== false) {
      loading = Loading.service({
        background: 'rgba(0, 0, 0, 0.1)'
      })
    }
    return config
  }, err => {
    return Promise.reject(err)
  })
  // 请求结束
  Axios.interceptors.response.use(response => {
    if (!response.data.success) {
      if (response.data.errorCode === 1001 || response.data.errorCode === 1002 || response.data.code === 1001) {
        Message.warning('请重新登录！')
        Store.dispatch('LOGOUT')
      }
    }
    if (response.config.loading !== false) {
      loading.close()
    }
    if (response.config.record) { // 配置了record:true 时会触发详情页中的动态接口 获取新数据
      Store.dispatch('DISPATCH_RECORD')
    }
    return response.data
  }, error => {
    loading && loading.close()
    Message.error(error.toString())
    return Promise.reject(error)
  })

  Vue.req = (() => {
    return Axios
  }).call(this)

  Object.defineProperties(Vue.prototype, {
    $req: {
      get () {
        return Axios
      }
    }
  })
}

if (typeof window !== 'undefined' && window.Vue) {
  window.Vue.use(plugin)
}

export default plugin
