import Vue from 'vue'

export const USER_LOGIN = 'USER_LOGIN'
export const GET_CAPTCHA = 'GET_CAPTCHA'
export const GET_CODE = 'GET_CODE'
export const CHECK_CODE = 'CHECK_CODE'
export const FIND_PASSWORD = 'FIND_PASSWORD'
console.log(window.location)
let host = window.location.origin
const actions = {
  [USER_LOGIN] ({commit, dispatch}, data) {
    return new Promise((resolve, reject) => {
      Vue.req.post(host + '/saas/login.json', data, {
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded'
        }
      }).then(res => {
        if (res.success || res.errorCode === 1004) {
          resolve(res)
        } else {
          resolve(res)
        }
      }).catch(err => {
        reject(err)
      })
    })
  },
  [GET_CAPTCHA] ({commit, dispatch}, data) {
    return new Promise((resolve, reject) => {
      Vue.req.post('/http/common/captchaByUuid.json', data, {
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded'
        }
      }).then(res => {
        resolve(res)
      }).catch(err => {
        reject(err)
      })
    })
  },
  [GET_CODE] ({commit, dispatch}, data) {
    return new Promise((resolve, reject) => {
      Vue.req.post('/http/common/sendCheckCode.json', data, {
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded'
        }
      }).then(res => {
        resolve(res)
      }).catch(err => {
        reject(err)
      })
    })
  },
  [CHECK_CODE] ({commit, dispatch}, data) {
    return new Promise((resolve, reject) => {
      Vue.req.post('/http/common/checkAuthCode.json', data, {
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded'
        }
      }).then(res => {
        resolve(res)
      }).catch(err => {
        reject(err)
      })
    })
  },
  [FIND_PASSWORD] ({commit, dispatch}, data) {
    return new Promise((resolve, reject) => {
      Vue.req.post('/http/saas/findPassword.json', data, {
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded'
        }
      }).then(res => {
        resolve(res)
      }).catch(err => {
        reject(err)
      })
    })
  }
}

export default {
  actions
}
