import Vue from 'vue'
export const WBS_NS = 'WBS_NS'
let host = window.location.origin
const actions = {
  [WBS_NS] ({commit, dispatch}, data) {
    return new Promise((resolve, reject) => {
      Vue.req.post(host + '/passport/api', data, {
        headers: {
          'Content-Type': 'application/json;charset=UTF-8',
          'api_new': 'true'
        },
        loading: false
      }).then(res => {
        resolve(res)
      }).catch(err => {
        reject(err)
      })
    })
  }
}
export default {
  actions
}
